@use "./partials/global" as *;

img {
  height: 100%;
  width: 100%;
}

#main__hero-wrapper {
  margin-top: 4rem;
  background-color: #FEFAF5;
}
.main__hero {

  width: 90%;
  margin: auto;
  padding: 2rem;

  h1 {
    width: 40%;
  }
}

.main__hero article {
  float: left;
  width: 55%;
  padding: 2rem;
}

