#section__hero-pic {
  width: 30%;
  height: auto;
}

.main__card-container {
  display: flex;
  gap: 2rem;
  width: 67vw;
  margin: auto;
  padding-bottom: 5rem;
  padding-top: 1rem;
}

.section__card {
  //     The border radius of the cards must be 5px
  // o The border of the cards must be 1px solid black with an opacity of 20%
  // o The box shadow of the cards must be:
  //  Horizontal Offset: 0px
  //  Vertical Offset: 4px
  //  Blur: 4px
  //  Color: Black, with an opacity of 20%

  border-radius: 5px;
  border: 1px solid black;
  box-shadow: 4px 0px 4px;
  color: black;
  flex-grow: 3;
}

.section__card p {
  position: relative;
  bottom: 4rem;
  right: 2rem;
  float: right;
  color: #ffffff;
  font-size: 1.17em;
  font-weight: 900;
}