body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

/* P = Active Page
BG = Background
TT = Title and Text
ST = Subtitles */
body {
  background-color: #FEFAF5;
}

main {
  background-color: #ffffff;
}

/* h6,
h5,
h4,
h3,
h2,
h1 {
   Write them in this order
} */
h6, h5, h4, h3, h2, h1, p {
  color: #335576;
}

#nav__logo,
.body__navbar a {
  color: #335576;
}

#nav__logo {
  order: -1;
  margin-right: auto;
  font-weight: 900;
}

.body__navbar {
  width: 100%;
  position: fixed;
  background-color: #ffffff;
  top: 0;
}
.body__navbar ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.body__navbar #ul__currentpage a {
  text-decoration: none;
  border: solid;
  border-color: #DB7A4E;
  border-top: 0;
  border-right: 0;
  border-bottom-width: 0.3em;
  border-left: 0;
}
.body__navbar li {
  margin: 1em;
}
.body__navbar ul li a {
  padding-bottom: 0.1rem;
  text-decoration: none;
}
.body__navbar ul li a:active {
  color: #DB7A4E;
}

footer {
  background-color: #FEFAF5;
  height: 20vh;
}

.footer__info {
  display: flex;
  justify-content: center;
  padding: 2rem;
}
.footer__info h3 {
  margin: 0;
}
.footer__info article {
  padding: 0 2em;
}
.footer__info ul {
  display: inline-flex;
  padding: 0;
  margin: 0;
}
.footer__info ul li {
  list-style: none;
  padding: 0;
  flex-grow: 2;
}
.footer__info ul li p {
  width: 100%;
  margin-right: 1rem;
}
.footer__info .footer__subtitle {
  font-size: 0.625rem;
  color: #DB7A4E;
  margin-top: 0.5rem;
}
.footer__info img {
  width: 2rem;
  height: 2rem;
  padding-right: 1rem;
  padding-top: 1rem;
}

img {
  height: 100%;
  width: 100%;
}

#main__hero-wrapper {
  margin-top: 4rem;
  background-color: #FEFAF5;
}

.main__hero {
  width: 90%;
  margin: auto;
  padding: 2rem;
}
.main__hero h1 {
  width: 40%;
}

.main__hero article {
  float: left;
  width: 55%;
  padding: 2rem;
}

#section__hero-pic {
  width: 30%;
  height: auto;
}

.main__card-container {
  display: flex;
  gap: 2rem;
  width: 67vw;
  margin: auto;
  padding-bottom: 5rem;
  padding-top: 1rem;
}

.section__card {
  border-radius: 5px;
  border: 1px solid black;
  box-shadow: 4px 0px 4px;
  color: black;
  flex-grow: 3;
}

.section__card p {
  position: relative;
  bottom: 4rem;
  right: 2rem;
  float: right;
  color: #ffffff;
  font-size: 1.17em;
  font-weight: 900;
}